// import "bootstrap/dist/css/bootstrap.min.css";
import { useState, useEffect } from "react";
import "./App.scss";
import { ToastContainer } from "react-toastify";
import { useDispatch } from "react-redux";
import { authActions } from "./store/auth";
import jwt_decode from "jwt-decode";
// import ShalomOlam, { f1, f2, f3 } from "./components/HelloWorld";
import HelloWorldComponent from "./components/HelloWorldComponent";
import LoginPage from "./pages/LoginPage";
import ConditionPage from "./pages/ConditionPage";
import RegisterPage from "./pages/RegisterPage";
import PanelPage from "./pages/PanelPage/PanelPage";
import HomePage from "./pages/HomePage";
import BizCardPage from "./pages/BizCardPage";
import SideEffectPage from "./pages/SideEffect";
import NavBarComponent from "./components/NavBarComponent";
import TKPageOne from "./pages/TKRedux/TkPageOne";
import TKPageTwo from "./pages/TKRedux/TkPageTwo";
import HWPageOne from "./pages/HWRedux/HWPageOne";
import HWPageTwo from "./pages/HWRedux/HWPageTwo";
import autoLogin from "./pages/service/autoLogin";
import { Route, Switch } from "react-router-dom";
import TKRedux1 from "./pages/TKRedux1";
import TKRedux2 from "./pages/TKRedux2";
import HWRedux1 from "./pages/HWRedux/HWRedux1";
import moreInfoBizCardPage from "./pages/MoreInfoBizCardPage";
import NotFoundPage from "./pages/NotFoundPage";
import AuthGuardRoute from "./components/AuthGuardRoute";
import TK3 from "./components/TK3";
import QParamsPage from "./pages/QParamsPage";
import MyQParamsPage from "./pages/TKRedux/MyQParamsPage";
import QParamsPageFilter from "./pages/QParamsPageFilter";
import ForgotPassword from "./pages/ForgotPassword.page";
import ResetPasswordPage from "./pages/ResetPasswordPage";

const App = () => {
  // const [tf, setTf] = useState(true);
  const dispatch = useDispatch();
  useEffect(() => {
    (async () => {
      try {
        let { data } = await autoLogin();
        let dataFromToken = jwt_decode(localStorage.getItem("token"));
        dispatch(authActions.login(dataFromToken));
        if (data) {
          dispatch(authActions.updateUserInfo(data));
        }
      } catch (err) {
        console.log("you not logged in");
      }
    })();
  }, []);

  return (
    <div className="container">
      <NavBarComponent />
      <ToastContainer />
      <Switch>
        <Route path="/" exact component={HomePage}></Route>
        <Route path="/login" component={LoginPage}></Route>
        <Route path="/register" component={RegisterPage}></Route>
        {/* <Route path="/panelpage" component={PanelPage}></Route> */}
        <AuthGuardRoute
          path="/panelpage"
          component={PanelPage}
        ></AuthGuardRoute>
        <AuthGuardRoute path="/TK3" component={TKRedux2}></AuthGuardRoute>
        <Route path="/moreinfo/:id" component={moreInfoBizCardPage}></Route>
        <Route path="/TKRedux1" component={TKRedux1}></Route>
        <Route path="/TKRedux2" component={TKRedux2}></Route>
        <Route path="/HWRedux1/:id" component={HWRedux1}></Route>
        <Route path="/qparams" component={QParamsPage}></Route>
        <Route path="/qparamsfilter" component={QParamsPageFilter}></Route>
        <Route path="/MyQParamsPage" component={MyQParamsPage}></Route>
        <Route path="/forgotpassword" component={ForgotPassword}></Route>
        <Route
          path="/resetpassword/:token"
          component={ResetPasswordPage}
        ></Route>
        <Route path="*" component={NotFoundPage}></Route>
      </Switch>
      {/* <HelloWorldComponent /> */}
      {/* <RegisterPage /> */}
      {/* <LoginPage /> */}
      {/* <ConditionPage /> */}
      {/* <PanelPage /> */}
      {/* <BizCardPage /> */}
      {/* <HomePage /> */}
      {/* <TKPageOne /> */}
      {/* <TKPageTwo /> */}
      {/* <HWPageOne /> */}
      {/* <HWPageTwo /> */}
      {/* <button onClick={(ev) => setTf(!tf)}>toggle timer</button>
      {tf && <SideEffectPage />} */}
    </div>
  );
};

export default App;
