import { createSlice } from "@reduxjs/toolkit";

const initialHWCounterState = {
    counter: 0,
};

const HWCounterSlice = createSlice({
    name: "HWCounter",
    initialState: initialHWCounterState,
    reducers: {
        randomNum(state) {
            state.counter = Math.floor(Math.random() * 100 + 1);
        },
    },
});

export const HWCounterActions = HWCounterSlice.actions;

export default HWCounterSlice.reducer;