import { useSelector } from "react-redux";
import { Route, Redirect } from "react-router-dom";

const GetRandomNumber = ({ component: Page, ...rest }) => {
  const randomNumber = useSelector(() => Math.random() % 2);
  return (
    <Route
      {...rest}
      render={(props) =>
        randomNumber ? (
          <Page {...props}></Page>
        ) : (
          <Redirect to="/TKRedux2"></Redirect>
        )
      }
    ></Route>
  );
};
console.log(Math.random() % 2);

export default GetRandomNumber;
